// moment
import moment from "moment"
// 
var mymoment = {}
mymoment.install = function(Vue){
    // 
    Vue.filter("myfilter",function(value,sptStr){
        // 
        return moment (value).format(sptStr)
    })
}
// 
export default mymoment


