<template>
  <div class="mycon">
    <el-container>
      <!--  -->
      <!-- Layout 24 -->
      <el-header>
        <tophead></tophead>
      </el-header>
      <el-container>
        <!--  -->
        <el-aside width="200px">
          <Aside/>
        </el-aside>
        <!--  -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
// tophead
import tophead from "../layout/tophead/tophead.vue";
// aside
import Aside from "../layout/aside/aside.vue";
export default {
  // token
  mounted() {
    var token = window.localStorage.getItem("token");
    // 
    if (!token) {
      // ,login
      this.$router.push("/login");
    }
  },
  components: {
    tophead,
    Aside
  }
};
</script>

<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #fff;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  /* text-align: center; */
  line-height: 200px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  /* text-align: center; */
  /* line-height: 160px; */
}
.el-container {
  height: 100%;
}
.mycon {
  height: 100%;
}
.altitude {
  height: 60px;
}

.mymenu {
  height: 100%;
}
</style>



