<template>
  <div>
    <!-- login -->
    <el-row class="altitude">
      <el-col :span="4">
        <div class="grid-content bg-purple">
          <img src="../../../../static/logo.png" alt="">
        </div>
      </el-col>
      <el-col :span="16" class="altitude">
        <!--  -->
        <div class="grid-content bg-purple-light">
          <h1></h1>
        </div>
      </el-col>
      <el-col :span="4" class="altitude lagout">
        <div class="grid-content bg-purple">
          <a href="#" @click.prevent="out"></a>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  methods: {
    out() {
      this.$confirm(",?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!"
          });
          // token
          window.localStorage.removeItem("token")
          // 
          this.$router.push({name:"login"})
        })
    }
  }
};
</script>

<style scoped>
.lagout {
  text-align: right;
}
.lagout a {
  /* a     */
  text-decoration: none;
  /*  */
  color: green;
}
.altitude{
  height: 60px;
}
</style>



