<template>
  <el-card>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="myrow">
      <el-col :span="6">
        <el-input placeholder="" v-model="search" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="searchdata"></el-button>
        </el-input>
      </el-col>
      <!--  -->
      <el-col :span="18">
        &nbsp;
        <el-button type="info" plain @click="add"></el-button>
      </el-col>
    </el-row>
    <!--  -->
    <el-table :border="true" :data="tableData" style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="username" label="" width="180"></el-table-column>
      <el-table-column prop="email" label="" width="180"></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-switch
            @change="switching(scope.row.id,scope.row.mg_state)"
            v-model="scope.row.mg_state"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!-- slot-scope = "scope" -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            plain
            @click="getedit(scope.row.id)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            plain
            @click="getDlelete(scope.row.id)"
          ></el-button>
          <el-button
            type="success"
            icon="el-icon-check"
            size="mini"
            plain
            @click="role(scope.row.id)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <!--  -->
    <el-dialog title="" :visible.sync="ShowHide">
      <el-form :rules="rules" ref="addForm" :model="formObj">
        <!--  -->
        <el-form-item prop="username" label="" :label-width="formLabelWidth">
          <el-input v-model="formObj.username" autocomplete="off"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password" label="" :label-width="formLabelWidth">
          <el-input type="password" v-model="formObj.password" autocomplete="off"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="email" label="" :label-width="formLabelWidth">
          <el-input v-model="formObj.email" autocomplete="off"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="mobile" label="" :label-width="formLabelWidth">
          <el-input v-model="formObj.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ShowHide = false"> </el-button>
        <el-button type="primary" @click="postAdd"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="modification ">
      <el-form :rules="rules" ref="addForm" :model="formObj">
        <!--  -->
        <el-form-item prop="username" label="" :label-width="formLabelWidth">
          <el-input :disabled="true" v-model="formObj.username" autocomplete="off"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="email" label="" :label-width="formLabelWidth">
          <el-input v-model="formObj.email" autocomplete="off"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="mobile" label="" :label-width="formLabelWidth">
          <el-input v-model="formObj.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="modification = false"> </el-button>
        <el-button type="primary" @click="postedit"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="assignment ">
      <el-form ref="addForm" :model="formObj">
        <!--  -->
        <el-form-item prop="username" label="" :label-width="formLabelWidth">{{formObj.username}}</el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          {{formObj.rid}}
          <el-select v-model="formObj.rid" placeholder="">
            <el-option :value="-1" label=""></el-option>
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="assignment = false"> </el-button>
        <el-button type="primary" @click="permission"> </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      pagenum: 1, // 
      pagesize: 10, // 
      tableData: [], // 
      formObj: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      ShowHide: false,
      modification: false,
      assignment: false, // 
      formLabelWidth: "100px",
      rules: {
        // 
        username: [
          { required: true, message: "", trigger: "blur" }
        ], // 
        password: [{ required: true, message: "", trigger: "blur" }], // 
        email: [{ required: true, message: "", trigger: "blur" }], // // 
        mobile: [{ required: true, message: "", trigger: "blur" }] // // 
      },
      options: [] // 
    };
  },
  methods: {
    // 
    getUserList() {
      //  token
      let token = window.localStorage.getItem("token");
      //  token
      this.$http
        .get("users", {
          headers: {
            //  token
            Authorization: token
          },
          params: {
            pagenum: this.pagenum,
            pagesize: this.pagesize,
            query: this.search
          }
        })
        .then(res => {
          // 
          let { data, meta } = res.data;
          if (meta.status === 200) {
            // 
            this.tableData = data.users;
          }
        });
    },
    add() {
      this.ShowHide = true;
    },
    // 
    postAdd() {
      let token = window.localStorage.getItem("token");
      // 1.0 
      this.$refs.addForm.validate(vali => {
        if (vali) {
          // 2.0 
          this.$http
            .post(
              "users",
              {
                username: this.formObj.username,
                password: this.formObj.password,
                email: this.formObj.email,
                mobile: this.formObj.mobile
              },
              {
                // token
                headers: {
                  Authorization: token
                }
              }
            )
            .then(res => {
              // 3.0 
              let { meta } = res.data;
              if (meta.status === 201) {
                this.$message({
                  type: "success",
                  message: meta.msg
                });
                this.getUserList();
                // 
                this.ShowHide = false;
                // 
                this.formObj.username = "";
                this.formObj.password = "";
                this.formObj.email = "";
                this.formObj.mobile = "";
              } else {
                this.$message.error(meta.msg);
              }
            });
        } else {
          this.$message.error("");
        }
      });
    },
    // 
    searchdata() {
      this.getUserList();
    },
    // 
    getedit(id) {
      this.modification = true;
      // id
      this.$http
        .get(`users/${id}`, {
          // Authorization
          // headers: {
          //   Authorization: localStorage.getItem("token")
          // }
        })
        .then(res => {
          // 
          let { meta, data } = res.data;
          // 
          if (meta.status === 200) {
            this.formObj = data;
          }
        });
    },
    // 
    postedit() {
      // 
      this.$http
        .put(
          `users/${this.formObj.id}`,
          {
            email: this.formObj.email,
            mobile: this.formObj.mobile
          },
          {
            // headers: {
            //   Authorization: localStorage.getItem("token")
            // }
          }
        )
        .then(res => {
          let { meta } = res.data;
          if (meta.status === 200) {
            this.modification = false;
            // 
            this.$message({
              type: "success",
              message: meta.msg
            });
            // 
            this.getUserList();
          } else {
            this.modification = false;
            this.$message.error(meta.msg);
          }
        });
    },
    // ID
    getDlelete(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          // id
          this.$http
            .delete(`users/${id}`, {
              headers: {
                Authorization: localStorage.getItem("token")
              }
            })
            .then(res => {
              // 
              let { meta } = res.data;
              // 
              if (meta.status === 200) {
                this.$message({
                  type: "success",
                  message: meta.msg
                });
                this.getUserList();
              } else {
                this.$message.error(meta.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    switching(id, state) {
      // ID
      this.$http
        .put(
          `users/${id}/state/${state}`,
          {},
          {
            // headers: {
            //   Authorization: localStorage.getItem("token")
            // }
          }
        )
        .then(res => {
          // 
          let { meta } = res.data;
          // 
          if (meta.status === 200) {
            this.$message({
              type: "success",
              message: meta.msg
            });
          } else {
            this.$message.error(meta.msg);
          }
        });
    },
    // 
    getoptions() {
      this.$http
        .get("roles", {
          // headers: {
          //   Authorization: localStorage.getItem("token")
          // }
        })
        .then(res => {
          this.options = res.data.data;
        });
    },
    // 
    role(id) {
      this.assignment = true;
      //  id 
      this.$http
        .get(`users/${id}`, {
          // headers: {
          //   Authorization: localStorage.getItem("token")
          // }
        })
        .then(res => {
          let { meta, data } = res.data;
          if (meta.status == 200) {
            this.formObj = data;
          } else {
            this.$message.error(meta.msg);
          }
        });
    },
    // 
    permission() {
      // 
      this.$http
        .put(
          `users/${this.formObj.id}/role`,
          {
            rid: this.formObj.rid
          },
          {
            // headers: {
            //     Authorization: localStorage.getItem('token')
            // }
          }
        )
        .then(res => {
          let { meta } = res.data;
          if (meta.status === 200) {
            this.$message({
              type: "success",
              message: meta.msg
            });
            // 
            this.getUserList();
          } else {
            this.$message.error(meta.msg);
          }
          this.assignment = false;
        });
    }
  },
  mounted() {
    this.getUserList();
    this.getoptions();
  }
};
</script>

<style scoped>
/* .myrow {
  margin-top: 20px;
} */
.el-breadcrumb {
  background-color: #d3dce6;
  height: 45px;
  font-size: 15px;
  padding-left: 10px;
  line-height: 45px;
}
</style>



