<template>
  <el-card>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row>
      <el-button type="info" plain></el-button>
    </el-row>
    <!--  -->
    <el-table :border="true" :data="rolesList" style="width: 100%">
      <!--  -->
      <el-table-column type="expand">
        <template slot-scope="scope">
          <!--  -->
          <el-row v-for="(item1, index1) in scope.row.children" :key="index1">
            <!--  -->
            <el-col :span="4">
              <el-tag
                @close="myclose(scope.row.id, item1.id, scope)"
                :disable-transitions="false"
                closable
              >{{item1.authName}}</el-tag>
              <i class="el-icon-arrow-right"></i>
            </el-col>
            <!--  -->
            <el-col :span="20">
              <el-row v-for="(item2, index2) in item1.children" :key="index2">
                <!--  -->
                <el-col :span="5">
                  <el-tag
                    @close="myclose(scope.row.id, item2.id, scope)"
                    :disable-transitions="false"
                    closable
                    type="success"
                  >{{ item2.authName }}</el-tag>
                  <i class="el-icon-arrow-right"></i>
                </el-col>
                <!--  -->
                <el-col :span="19">
                  <el-tag
                    @close="myclose(scope.row.id, item3.id, scope)"
                    :disable-transitions="false"
                    class="mytag3"
                    type="warning"
                    closable
                    v-for="(item3, index3) in item2.children"
                    :key="index3"
                  >{{ item3.authName }}</el-tag>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="roleName" label="" width="180"></el-table-column>
      <el-table-column prop="roleDesc" label="" width="180"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!-- slot-scope = "scope" -->
          <el-button type="primary" icon="el-icon-edit" size="mini" plain></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" plain></el-button>
          <el-button
            type="success"
            @click="authority(scope)"
            icon="el-icon-check"
            size="mini"
            plain
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <!--  props: &
                                    show-checkbox: 
                                    data: 
                                    default-expand-all: 
                                    default-checked-keys
    node-key-->
    <el-dialog title="" :visible.sync="dialogList">
      <!--  -->
      <el-tree
        :data="rightsList"
        :props="props"
        node-key="id"
        ref="tree"
        :default-checked-keys="defaultcheck"
        default-expand-all
        show-checkbox
      ></el-tree>
      <div slot="footer" class="dialog-footer">
        <!--  -->
        <el-button @click="dialogList = false"> </el-button>
        <el-button type="primary" @click="dialogto"> </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [], // 
      dialogList: false, // 
      defaultcheck: [], // 
      rightsList: [], // 
      props: {
        label: "authName",
        children: "children"
      },
      rid: ""
    };
  },
  methods: {
    // 
    async getdata() {
      let res = await this.$http.get("roles");
      // 
      let { meta, data } = res.data;
      // 
      if (meta.status === 200) {
        this.rolesList = data;
      } else {
        this.$message.error(meta.msg);
      }
      // console.log(res.data);
    },
    // 
    //  roleId  id
    //  rightId  id
    //  scope 
    async myclose(roleId, rightId, scope) {
      let res = await this.$http.delete(`roles/${roleId}/rights/${rightId}`);
      // 
      let { meta, data } = res.data;
      // 
      if (meta.status === 200) {
        // 
        scope.row.children = data;
      } else {
        this.$message.error(meta.msg);
      }
    },
    // ,
    async authority(scope) {
      // id
      this.rid = scope.row.id;
      // 
      this.defaultcheck = [];
      // 
      this.dialogList = true;

      // 
      let res = await this.$http.get(`rights/tree`);
      // 
      let { meta, data } = res.data;
      // 
      if (meta.status === 200) {
        this.rightsList = data;
        // 
        // 
        scope.row.children.forEach(item1 => {
          // 
          item1.children.forEach(item2 => {
            // 
            item2.children.forEach(item3 => {
              // ID
              this.defaultcheck.push(item3.id);
            });
          });
        });
      } else {
        this.$message.error(meta.msg);
      }
    },
    // 
    async dialogto(){
      // 
      // getCheckdkeys: key
      let allchek = this.$refs.tree.getCheckedKeys()
      let half = this.$refs.tree.getHalfCheckedKeys()
      // 
      let arr = [...allchek,...half]
      // 
      let rids = arr.join(",")
      // 
      let res =  await this.$http.post(`roles/${this.rid}/rights`,{
        rids:rids
      })
      // 
      let {meta} = res.data
      if(meta.status === 200){
        this.$message({
          type:"success",
          message:meta.msg
        })
        // 
        this.getdata()
      }else{
        this.$message.error(meta.msg)
      }
      // 
      this.dialogList = false
    }
  },
  mounted() {
    this.getdata();
  }
};
</script>

<style scoped>
.el-breadcrumb {
  background-color: #d3dce6;
  height: 45px;
  font-size: 15px;
  padding-left: 10px;
  line-height: 45px;
}
.mytag3 {
  margin-right: 10px;
  margin-bottom: 10px;
}
</style>



