<template>
  <el-card>
    <!--  -->
    <mygoods :navone="''" :navtow="''"></mygoods>
    <!--  -->
    <el-row class="myrow">
      <el-col :span="6">
        <el-input placeholder="" v-model="search" class="input-with-select">
          <el-button slot="append" @click="mysearch" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
      <!--  -->
      <el-col :span="18">
        &nbsp;
        <el-button type="info" plain @click="goodsadd"></el-button>
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="tableData" style="width: 100%" :border = "true">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="goods_name" label="" width="400px"></el-table-column>
      <el-table-column prop="goods_price" label=""></el-table-column>
      <el-table-column prop="goods_number" label=""></el-table-column>
      <el-table-column label="">
          <template slot-scope="scope">
              {{scope.row.add_time | myfilter("YYYY-MM-DD hh:mm:ss")}}
          </template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <el-button type="primary" icon="el-icon-edit" plain size="mini"></el-button>
        <el-button type="danger" icon="el-icon-delete" plain size="mini"></el-button>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination 
         @current-change="handleCurrentChange"
           @size-change="handleSizeChange"
        :current-page="currentPage" 
        :page-sizes="pageSizes" 
        :page-size="pageSize" 
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>
  </el-card>
</template>

<script>
// 
import mygoods from "../layout/breadnav/breadnav.vue";
export default {
    data() {
        return {
            tableData:[] , // 
            currentPage:1, // 
            pageSize:10, // 
            pageSizes:[10,20,30,40,50,60,70,80] ,// 
            total:0, // 
            search:"" // 

        }
    },
  methods: {
    // 
    async getData(){
        let res = await this.$http.get(`/goods?pagenum=${this.currentPage}&pagesize=${this.pageSize}&query=${this.search}`)
        // 
        let {meta,data} = res.data
        //  
        if(meta.status === 200){
            // 
            this.total = data.total
            // 
            this.tableData = data.goods
        }else{
            this.$message.error(meta.msg)
        }
        
    },
    mysearch(){
        this.getData()
    },
    handleCurrentChange(val){
        this.currentPage = val
        // 
         this.getData()
    },
    handleSizeChange(val){
        this.pageSize = val
        // 
         this.getData()
    },
    goodsadd(){
        // 
        this.$router.push("/goods/add")
    }
  },
  mounted() {
      this.getData()
  },
  components: {
    mygoods
  }
};
</script>

<style>
</style>



