<template>
  <el-card>
    <!--  -->
    <crumbs :navone="''" :navtow="''"></crumbs>
    <!--  -->
    <el-alert class="myalter" title="" type="info" center show-icon></el-alert>
    <!--  -->
    <el-steps :active="active" finish-status="success" align-center class="mysteps">
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>
    <!--  -->
    <el-tabs :tab-position="tabPosition" @tab-click="tabclick">
      <!--  -->
       <el-tab-pane label="" name="first">
        <!--  -->
        <el-form :label-position="'top'" :rules="rules" label-width="80px" :model="addObj">
          
          <el-form-item label="" prop="goods_name">
            <el-input v-model="addObj.goods_name"></el-input>
          </el-form-item>

          <el-form-item label="" prop="goods_price">
            <el-input v-model="addObj.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_weight">
            <el-input v-model="addObj.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_number">
            <el-input v-model="addObj.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-cascader v-model="cateSelect" :options="catesList" :props="cateObj"></el-cascader>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <div class="mydiv" v-for="(item, index1) in manyparams" :key="index1">
          <h4>{{ item.attr_name }}</h4>
          <div class>
            <el-checkbox
              v-model="check"
              border
              v-for="(val, index2) in item.attr_vals.split(',')"
              :key="index2"
            >{{ val }}</el-checkbox>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="">
        <div class="mydiv" v-for="(item,index) in onlyparams" :key="index">
          <h4>{{item.attr_name}}</h4>
          <el-input v-model="item.attr_vals" placeholder=""></el-input>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="fourth">
        <!--  -->
        <el-upload
          class="upload-demo"
          action="http://localhost:8888/api/private/v1/upload" 
          :file-list="fileList"
          :on-success="uploadsuccess" :on-remove="remove"
          :on-preview="preview"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
      </el-tab-pane>
      <el-tab-pane label="" name="diwu">
        <el-button @click="addgoods"></el-button>
        <quill-editor v-model="content"></quill-editor>
      </el-tab-pane>
    </el-tabs>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogImg">
      <img ref="myimg">
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogImg = false"> </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
// 
import crumbs from "../layout/breadnav/breadnav.vue";
export default {
  data() {
    return {
      active: 0,
      catesList: [], // 
      cateSelect: [], // 
      manyparams: [], // 
      onlyparams: [], // 
      fileList:[], // 
       content: '',
      dialogImg:false,
      uploadHead: {
        Authorization: localStorage.getItem('token')
      }, // 
      check: true,
      tabPosition: "left",
      addObj: {
        // 
        goods_name: "",
        goods_price: "",
        goods_weight: "",
        goods_number: ""
      },
      rules: {  // 
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cateObj: {
        value: "cat_id",
        label: "cat_name"
      }
    };
  },
  methods: {
    // 
    async getList() {
      // 
      let res = await this.$http.get(`categories?type=3`);
      // 
      let { meta, data } = res.data;
      // 
      if (meta.status === 200) {
        this.catesList = data;
      } else {
        this.$message.error(meta.msg);
      }
    },
    // tab
    tabclick(tab, event) {
      this.active = +tab.index;
      if (tab.index === "1" || tab.index === "2") {
        // , 
        if (this.cateSelect.length === 0) {
          this.$message.error("");
          return;
        }
        this.getCateParams(tab.index);
      }
    },
    // 
    async getCateParams(index) {
      // ID
      let id = this.cateSelect[this.cateSelect.length - 1];
      // 
      let sel = index === "1" ? "many" : "only";
      // 
      let res = await this.$http.get(`categories/${id}/attributes?sel=${sel}`);
      // 
      let { meta, data } = res.data;
      // 
      if (meta.status === 200) {
        if (index === "1") {
          // 
          this.manyparams = data;
        } else {
          this.onlyparams = data;
        }
      } else {
        this.$message.error(meta.msg);
      }
    },
    // 
   uploadsuccess(res, file, fileList) {
      //  fileList 
      // res: 
      //  tmp_path: 
      //  url: 
      // file:  res
      // fileList:  file
      this.fileList.push({
        name: res.data.tmp_path,
        url: res.data.url
      })
    },
    // 
    remove(file,fileList){
      //  file  fileList 
      for (let i = 0; i < this.fileList.length; i++) {
        if (this.fileList[i].name === file.name) {
          this.fileList.splice(i,1)
        }
        
      }
    },
    // 
    preview(file){
      this.dialogImg = true
      this.$nextTick(function() {
        this.$refs.myimg.src = file.url
      })
    },
    // 
   async addgoods(){
      let addObj = {
        goods_name:this.addObj.goods_name, // 
        goods_cat:this.cateSelect.join(","), // 
        goods_price:this.addObj.goods_price, // 
        goods_number:this.addObj.goods_number, // 
        goods_weight:this.addObj.goods_weight , // 
        goods_introduce:this.content
      }
      let res = await this.$http.post("goods",addObj)
      // 
      let {meta} = res.data
      if(meta.status === 201){
        this.$message({
          type:"success",
          message:meta.msg
        })
        this.$router.push({name:"goods"})
      }else{
        this.$message.error(meta.msg)
      }
    }
  },
  components: {
    crumbs
  },
  mounted() {
    this.getList();
  }
};
</script>

<style>
.mysteps,
.myalter {
  margin-top: 20px;
}
.mydiv h4 {
  font-weight: 400px;
  font-size: 13px;
}

.ql-editor {
  height: 300px;
}
</style>



