<template>
    <el-card>
        <!--  -->
        <MyBread :navone="''" :navtow="''" />
        <!--  -->
        <el-row>
            <el-col :span="4">
                <el-button class="mybtn" type="success" plain></el-button>
            </el-col>
        </el-row>
        <!--   -->
        <el-table :data="pageList" style="width: 100%">
            <el-table-tree-column file-icon="icon icon-file" folder-icon="icon icon-folder" prop="cat_name" label="" treeKey="cat_id" parentKey="cat_pid" levelKey="cat_level">
            </el-table-tree-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{ scope.row.cat_level === 0 ? '': scope.row.cat_level === 1 ? '' : '' }}
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{ scope.row.cat_deleted ? '': ''}}
                </template>
            </el-table-column>
            <el-table-column label="">
                <template>
                    <el-button type="primary" icon="el-icon-edit" plain size="mini"></el-button>
                    <el-button type="danger" icon="el-icon-delete" plain size="mini"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <!-- 
            el-pagination
                size-change
                current-change
                current-page
                page-sizes
                page-size
                layout
                total
            -->
        <el-pagination :current-page="currentpage" 
        :page-sizes="pagesizes" :page-size="pagesize" 
        layout="total, sizes, prev, pager, next, jumper" 
        @current-change="currentChange" 
        @size-change="sizeChange"
         :total="total">
        </el-pagination>
    </el-card>
</template>
<script>
// 
import MyBread from '../layout/breadnav/breadnav.vue'

export default {
    data() {
        return {
            cateList: [], // 
            pageList: [], // 
            total: 0, // 
            pagesize: 5, // 
            1: [5, 10, 15], // 
            currentpage: 1 //  
        }
    },
    components: {
        MyBread
    },
    methods: {
        async getCateList() {
            // 
            let res = await this.$http.get('categories?type=3')
            // 
            let { meta, data } = res.data
            // 
            if (meta.status === 200) {
                this.cateList = data
                // 
                this.total = data.length
                // 
                this.getPageList()
            } else {
                this.$message.error(meta.msg)
            }
        },
        // 
        currentChange(val) {
            // val 
            this.currentpage = val
            // 
            this.getPageList()
        },
        // 
        getPageList() {
            // 
            let start = (this.currentpage - 1) * this.pagesize
            let end = this.currentpage * this.pagesize
            // 
            this.pageList = this.cateList.slice(start, end)
        },
        // 
        sizeChange(val) {
            // val 
            this.pagesize = val
            // 
            this.getPageList()
        }
    },
    mounted() {
        this.getCateList()
    }
}
</script>

<style>
</style>



