<template>
  <!-- 

     el-form: 
        model: 
        status-icon
        :rules: 
        ref:  vue  this.$refs  dom 
        abel-width
        label-positio: 
        el-form-item: 
        label: 
        prop: 
        el-inputinput 
        ype
        v-model
        autocomplete h5
  -->
  <div class="loginbox">
    <el-form
      label-position="top"
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="username">
        <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="btn" type="primary" @click="login"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    login() {
      // form
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          // 
          this.$http.post("login", this.ruleForm).then(res => {
            // 
            let { data, meta } = res.data;
            //
            if (meta.status === 200) {
              this.$message({
                message: meta.msg,
                type: "success"
              });
              // 
              this.$router.push("/home")
             //    token  localstorage 
            localStorage.setItem('token', res.data.data.token)
            } else {
              this.$message.error(meta.msg);
            }
          });
        }
        // valid boolan  true false
         else {
          this.$message.error("");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.loginbox {
  padding: 35px;
  width: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 10px;
}
.loginbox .btn {
  width: 100%;
}
</style>



